}
}
.backdrop {
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background-color: #e179914a;
display: flex;
justify-content: center;
align-items: center;
z-index:10;

transform: translatex(0);
transition-property: transform;
transition-duration: 1s;
transition-timing-function: ease-in-out;
display:flex;
}
  
.modal-text {
padding-top: 40px;
padding-bottom:40px;
padding-left: 20px;
padding-right: 20px;
letter-spacing:2px;
font-family: $main-font;
font-style: normal;
font-weight: 500;
font-size: 12px;
z-index: 12;

