&__first {
color: $first-button-hover;
margin: 0;
text-align: left;
z-index: 12;
}
    

&__second{
color: $fifth-font-color;
margin: 0;
padding-top: 27px;
max-width: 100%;
text-align: left;
z-index: 12;
}

&__third{
margin: 0;
color: $first-button-hover;
margin: 0;
max-width: 100%;
text-align: left;
padding-top: 10px;
z-index: 12;
}
}
.about-milk-background-modal {
position: absolute;
left: 0;
top: 266px;
width: 100%;
z-index: 11;
background-repeat: no-repeat;
}

.about-milk-image-modal {
width: 100%;
z-index: 11;
position:fixed;
background-position: center;
}
.about-milk-icon-box {
  position: relative;
  width: 75%;
  height: 50px;
  padding-top: 400px;
}

.about-milk-icon-modal {
  z-index: 11;
  position: absolute;
  left: calc(50% - 17px);
  visibility: visible;
  transition: transform 1s ease-in-out;

  &:hover,
  &:focus {
    transform: scale(1.2);
  }

  @include tablet {
    bottom: -5px;
  }

  @include desktop {
    bottom: -10px;
  }
}

   
  